@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wapper__bsgame-container{
    font-family: 'Baloo Tammudu 2';
}
.info-user {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.info-user > p{
    font-size: large;
    font-weight: 700;
}
.info-user > p > span {
   color: darkred;
}
.dices {
    display: flex;
    justify-content: center;
    align-items: center;
}
.dices > span {
    font-size: xx-large;
    margin: 20px;
}
.dices > .dice {
    padding: 10px 30px 10px 30px;
    color: deepskyblue;
    background-color: rgba(170, 233, 238, 0.418);
    border-radius: 10px;
}
.total{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: xx-large;
    color: lightskyblue;
    margin-bottom: 10px;
}
.table{
    border:3px dashed teal;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    margin-top: 20px;
    font-size: 24px;
    color: red;
    flex-direction: column;
}
.table_{
    display: flex;
    justify-content: center;
    align-items: center;
}
.table__small{
    display: flex;
    justify-content: center;
    align-items: center;
  margin-right: 20px;
  border:3px dotted red;
  width: 300px;
  flex-direction: column;
}
.table__small > span {
    font-size: 16px;
    color: darksalmon;
}
.table__big{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    border:3px dotted red;
    width: 300px;
    flex-direction: column;
}
.table__big > span {
    font-size: 16px;
    color: darksalmon;
}
.table_amout_group {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}
@media only screen and (max-width: 768px){
    .table__big{
        width: 250px;
    }
    .table__small{
        width: 250px;
    }
  }
@media only screen and (max-width: 480px){
    .table__big{
        width: 150px;
    }
    .table__small{
        width: 150px;
    }
}

.current-time{
    display: flex;
    justify-content: center;
}

.current-time > p{
    color: mediumturquoise;
    font-size: 24px;
}
.probaliti{
    margin-top: 30px;
}
.probaliti__form-group--submit {
    display: flex;
    justify-content: center;
}
.probaliti__form-group--submit > input {
    margin-right: 15px;
    padding: 4px;
    width: 120px;
}
.probaliti__result{
    margin-top: 30px;
}
.probaliti__result > .probaliti__result--table{
    width: 100%;
    max-width: 100%;
    margin-bottom: 1rem;
    background-color: transparent;
}
.probaliti__result > .probaliti__result--table th,
.probaliti__result > .probaliti__result--table td{
    padding: 0.75rem;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
    text-align: center;
}
.probaliti__result > .probaliti__result--table > thead th {
    vertical-align: bottom;
    border-bottom: 2px solid #dee2e6;
}


.header{
    background-color: #8E05FA;
    font-size: 36px;
    font-family: 'Baloo Tammudu 2';
    height: 92px;
}
.header__nav{
    display: flex;
    align-items: center;
}
.header__nav > ul{
    list-style-type: none;
}
.header__nav > ul > li{
    display: inline-block;
    margin-right: 24px;
}
.header__nav > ul > li > a{
    color: #ffff;
    text-decoration: none;
}
.header__nav > ul > li > a:hover{
    color: #E35610;
    border-bottom: 5px solid #ffff;
}
.header__nav__surplus {
    margin-left: auto;
}
.wapper {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
  }
  
  @media (min-width: 576px) {
    .wapper {
      max-width: 540px;
    }
  }
  
  @media (min-width: 768px) {
    .wapper {
      max-width: 720px;
    }
  }
  
  @media (min-width: 992px) {
    .wapper {
      max-width: 960px;
    }
  }
  
  @media (min-width: 1200px) {
    .wapper {
      max-width: 1140px;
    }
  }
